<template>
	<div class="listMenu">
		<ul class="list">
			<li @mouseover="showChildMenu(1)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(2)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(3)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(4)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(5)">3C<span id="arrows"></span></li>
			<li @mouseover="showChildMenu(6)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(7)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(8)"><span id="arrows"></span></li>
			<li @mouseover="showChildMenu(9)"><span id="arrows"></span></li>
		</ul>
		<div class="listContent">
			<a  v-for="item in arr">{{item}}</a>
		</div>
	</div>
</template>

<script>
	export default{
		name:"",
		data(){
			return{
				menuArr:[["","","","","","","",""],["","","","","","",""],["","KTV","Bar","","","",""],[""],["","","","","","",""],["","SPA","","","",""],["",'',"","","",""],["","","","","",""],["","","PC",""]],
				arr:["","","","","","","",""],
			}
		},
		methods:{
			showChildMenu(n){
				this.arr=this.menuArr[n-1];
			}
		}
	}
</script>

<style lang="less" scoped>
	.listMenu{
		position: absolute;
		z-index: 100;
		left: 0;
		top: 48px;
		height: 472px;
		width: 576px;
		background: rgba(0,0,0,0.5);
		overflow: hidden;
		border-radius: 1px;
		display: flex;
		justify-content: space-between;
		font-size: 15px;
				font-weight: 100;
		.list{
			background:rgba(0,0,0,0.5);
			width: 175px;
			li{
				
				
				height: 52px;
				line-height: 52px;
				border-bottom: 1px solid rgba(255,255,255,0.2);
				cursor:pointer;
				text-align: left;
				padding:0 40px;
				display: flex;
				justify-content: space-between;
				align-items:center;
				&:hover{
					background: #f46b2c;
				}
				#arrows{
					display: inline-block;
					width: 7px; 
					height: 7px;
					border-top: 1px solid white;
					border-right: 1px solid white;
					margin-bottom: 1px;
					transform: rotateZ(45deg);
					/*vertical-align: middle;*/				
					margin-left: 25px;
					}
			}
		}
		.listContent{
			padding:30px 30px 30px 20px;
			text-align: left;
			width: 350px;
			a{
				padding:0 20px;
				display: inline-block;
				height: 14px;
				line-height: 14px;
				border-right: 1px solid white;
				&:hover{
					color: orange;
					text-decoration: underline;
					cursor: pointer;
				}
			}
		}
	}
</style>


